/* Footer styles */
.footer {
    background-color: #232f3e;  /* Amazon's official blue background color */
    color: #ffffff;            /* Text color */
    padding: 20px;
    
   
              
}


.footer-container {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;          
    margin: 0 auto;           
}

.footer-column {
    flex: 1; 
}


.footer-column h3 {
    color: #ffffff;   
    margin-bottom: 10px;         
}


.footer-links {
    list-style: none;        
    padding: 0;
    font-size: 13px;
}

.footer-links li {
    margin-bottom: 8px;        
}

.footer-links a {
    text-decoration: none;     /* Remove underlines from links */
    color: #cccccc;            /* Link color */
    transition: color 0.3s;     /* Smooth color transition on hover */
}

.footer-links a:hover {
    color: #ffffff;            /* Change color on hover */
}

/* Additional styling for social media icons, adjust as needed */
.social-icons {
    display: flex;
}

.social-icons a {
    margin-right: 10px;        /* Space between each social media icon */
    font-size: 20px;           /* Adjust the size of the icons */
    color: #cccccc;            /* Icon color */
    transition: color 0.3s;     /* Smooth color transition on hover */
}

.social-icons a:hover {
    color: #ffffff; 
}  
.copyright-text{
    text-align: center;
    font-size:14px; 
} 
.copyright-text a{
    color: #cccccc;
}

.container{
 
    margin-top: 40px;
